.notepage {
  height: 100vh;
  overflow: hidden;
}
.notepage__wrapper {
  height: 100vh;
  width: 100vw;
  overflow-y: scroll;
}
.notepage__container {
  margin: 0 auto;
  max-width: 75rem;
  padding: 0 2.5rem;
}
.notepage__kosong {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.notepage__list {
  padding: 4.375rem 0 6.25rem 0;
}